define('yaroom-tablet/routes/room', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Route.extend({
    queryParams: {
      room_id: {
        refreshModel: true
      }
    },
    model: function model(params) {
      var room = this.store.find('room', params.room_id);
      return room;
    }
  });

});